/*  sofax services section v3 */
.sofax-service-iconbox {
    &-wrap {
        display: flex;
        background-color: #fff;
        border-radius: 5px;
        padding: 30px;
        margin-bottom: 24px;
        @include max(767px){
            display: block;
        }
        &.version7{
            border: 1px solid var(--light-color2);
        }
    }
    &-data h4 {
        margin-bottom: 15px;
    }
    &-data p {
        font-size: 20px;
    }
    &-icon {
        flex-shrink: 0;
        @include max(767px){
            margin-top: 24px;
        }
    }
    
}
// sofax services v4
.sofax-service-table{
    &-item{
        display: flex;
        padding: 35px 0;
        border-bottom: 1px solid #EAEDF0;
        &:first-child{
            padding-top: 0;
        }
        &:last-child{
            padding-bottom: 0;
            border: none;
        }
        @include max(991px) {
            display: block;
        }
        
    }
    &-title{
        flex: 0 0 36%;
        h3{
            margin: 0;
            font-size: 35px;
            @include max(1199px) {
                font-size: 30px;
            }
        }
    }
    &-body{
        flex: 0 0 45%;
        padding: 0 30px;
        @include max(991px) {
            padding: 24px 0;
        }
        p{
            font-size: 20px;
        }
    }
    &-btn{
        flex: 0 0 19%;
        text-align: right;
        @include max(991px) {
            text-align: left;
        }
        .sofax-default-btn.outline-btn{
            padding: 17px 60px;
        }
        &:hover  .sofax-default-btn.outline-btn{
            background-color: var(--accent-color);
            border: 1px solid var(--accent-color);
        }
    }

    &-titlev5 {
        flex: 0 0 42%!important;
        @include max(1199px) {
            font-size: 30px;
        }
    }
    &-bodyv5{
        flex: 0 0 46%;
        padding: 0 38px!important;
        @include max(991px) {
            padding: 24px 0!important;
        }
        p{
            font-size: 20px!important;
        }
    }
    &-btnv5{
        flex: 0 0 12%!important;
        display: flex;
        justify-content: end;
        @include max(991px) {
            justify-content: start;
            display: block;
        }
        .p15{
            padding: 15px !important;
            width: 80px !important;
            height: 80px !important;
            display: flex;
            justify-content: center;
            align-items: center;
            @include max (1199px){
                width: 65px!important;
                height: 65px!important;
            }
            @include max (991px){
                width: 60px!important;
                height: 60px!important;
            }
        }
    }
}



// sofax service slider

.sofax-service-slider{
    background: var(--dark-bg);
    padding: 50px 0;
    &-wrap {
        display: flex!important;
        gap: 30px;
        margin-right: 24px;
    }
    &-icon{
        display: flex;
        align-items: center;
        animation: animName 3s linear infinite;
    }
}
.inner{
    &-service1{
        p{
            font-size: 18px;
        }
    }
    &-service2{
        padding: 25px;
        border-radius: 10px;
        h3{
            margin-bottom: 10px;
        }
        p{
            color: var(--light-color)!important;
        }
    }
}

.sofax-inner-service-details {
    h2{
        max-width: 850px;
    }
    p{
        font-size: 20px;
        margin-bottom: 30px;
    }
}
.sofax-inner-service-content{
    &-data{
        margin-bottom: 24px;
        &:last-child {
            margin-bottom: 0;
        }
        h4{
            margin-bottom: 15px;
        }
    }
}