.sofax-footer-top {
    padding: 130px 0 90px;
    @include max (991px){
        padding: 100px 0 70px;
    }
    @include max (767px){
        padding: 70px 0 50px;
    }
  }
  .sofax-footer-wrap{
    margin-right: 15px;
    @include max(1199px) {
        max-width: 500px;
    }
   
    img {
        margin-bottom: 20px;
    }
    p {
        margin-bottom: 24px;
        font-size: 20px!important;
    }
    &.light-color {
        color: var(--white-color);
    }
    &.ml-60{
        @include max (1199px) {
            margin-left: 0;
            max-width: 600px;
        }
    }
  }
 .mr-15{
    margin-right: 15px;
    @include max(991px) {
        margin-right: 0;
    }
 }
 .mr-25{
    margin-right: 25px;
    @include max(991px) {
        margin-right: 0;
    }
 }
  .sofax-footer-menu {
    h5 {
        margin-bottom: 24px;
    }
    ul {
        li{
            margin-bottom: 15px;
            &:last-child{
                margin-bottom: 0;
            }
            a{
                color: var(--body-color);
                position: relative;
                transition: all 0.4s;
            }
        }
    }
    @include max (1199px){
        margin-top: 30px;
    }
    &.light-color{
        h5{
            color: var(--white-color);
        }
        ul {
            li{
                a{
                    color: var(--white-color);
                }
            }
        }
    }
  }
.mr-15 {
    margin-right: 15px;
}
.sofax-footer-menu.light-color ul li a::before {
    background: var(--white-color);
}

.sofax-footer-menu ul li a:hover:before {
    width: 100%;
}
.sofax-footer-menu ul li a:before {
    content: " ";
    left: 0;
    bottom: 0;
    width: 0;
    height: 1px;
    position: absolute;
    transition: all .4s;
    background: var(--heading-color);
}
  .sofax-footer-active {
    margin-left: 60px;
    
    @include max (1199px){
        margin-left: 0px;
    }
  }
  
  a.social-icon img {
    margin-bottom: 15px;
  }


  .sofax-footer-bottom {
    display: block;
    padding: 30px 0;
    border-top: 1px solid var(--light-color);
    &.center{
        text-align: center;
    }
    p {
        margin: 0;
    }
    &.dark-bottom{
        border-top: 1px solid var(--body-color);
        p{
            color: var(--white-color);
        }
    }
  }

  // end sofax footer v1

.sofax-footer {
    @include max(991px) {
        margin-left: 30px;
    }
    @include max(991px) {
        margin-left: 0;
    }
}

// end sofax footer v2
.sofax-footer3 {
    &-wrap {
        display: flex;
        justify-content: space-between;
        padding: 30px 0;
        border-top: 1px solid var(--light-color);
        @include max (767px){
            display: block;
            text-align: center;
        }
    }
    &-text{
        align-items: center;
        display: flex;
        @include max (767px){
            display: block;
            margin: 15px 0 0 30px;
        }
    }
    p {
        line-height: 28px;
        color: var(--grey-color);
    }
}
//  sofax footer v5
.sofax-footer-titlev5 {
    @include max (991px){
        text-align: center;
    }
    h2{
        max-width: 785px;
        @include max (1199px){
            max-width: 600px;
        }
        @include max (991px){
            max-width: 100%;
        }
    }
    
    
}


.sofax-footer-titlev5 {
    padding: 100px 0;
    border-bottom: 1px solid var(--light-color);
    @include max (991px){
        padding: 80px 0;
    }
    @include max (767px){
        padding: 60px 0;
    }
}
.sofax-footer-title-btn{
    &-wrap {
        @include max(991px){
            display: flex;
            justify-content: center;
            width: 100%;
            margin-top: 30px;
        }
    }
    &-icon {
        padding: 15px;
        border: 1px solid var(--dark-bg);
        width: 80px;
        height: 80px;
        border-radius: 50px;
        display: flex;
        align-items: center;
        justify-content: center;
        &:hover {
            border: 1px solid var(--accent-color);
            background-color: var(--accent-color);
        }
        @include max (1199px){
            width: 70px;
            height: 70px;
         }
        @include max (991px){
           width: 60px;
           height: 60px;
        }
    }
}
