
.sofax-section-padding {
    padding: 130px 0 106px;
    @include max(991px) {
        padding: 100px 0 76px;
    }
    @include max(767px) {
        padding: 80px 0 56px;
    }
}
.sofax-section-padding5{
    padding-top: 130px;
    @include max(991px) {
        padding-top: 90px;
    }
    @include max(767px) {
        padding-top: 60px;
    }
}
.sofax-section-padding2 {
    padding: 130px 0;
    @include max(991px) {
        padding: 90px 0;
    }
    @include max(767px) {
        padding: 60px 0;
    }
}
.sofax-section-padding3 {
    padding: 0 0 130px;
    @include max(991px) {
        padding: 0 0 90px;
    }
    @include max(767px) {
        padding: 0 0 60px;
    }
}
.sofax-section-padding4 {
    padding: 180px 0 100px;
    @include max (991px){
        padding: 160px 0 70px;
    }
    @include max (767px){
        padding: 120px 0 40px;
    }
}
.sofax-section-title {
    p{
        font-size: 20px;
    }
    padding-bottom: 80px;
    @include max(991px) {
        padding-bottom: 60px;
        text-align: center;
    }
    @include max(767px) {
        padding-bottom: 40px;
    }
    &.center{
        max-width: 650px;
        margin: 0 auto;
        text-align: center;
        &.max-width-medium {
            max-width: 800px;
        }
        &.max-width700 {
            max-width: 700px;
        }
        &.max-width-large {
            max-width: 830px;
        }
        &.max-width-large2 {
            max-width: 730px;
        }
    }
}
.pb-60{
    padding-bottom: 60px;
    @include max (991px){
        padding-bottom: 40px;
    }
}
.max-width1200{
    max-width: 1200px;
}
.sofax-title-btn {
    @include max(991px) {
        width: 100%;
        text-align: center;
        margin-top: 30px;
    }
    
}

.extra-mt {
    margin-top: 50px;
    @include max (991px){
        margin-top: 40px;
    }
}
.sofax-default-content {
    h2 {
        margin-bottom: 20px;
    }
    p {
        font-size: 20px;
    }
  
}
.mr-80{
    margin-right: 80px;
    @include max(991px) {
        margin-right: 0;
    }
}
.ml-50{
    margin-left: 50px;
    @include max (991px){
        margin-left: 0;
    }
}
.mr-50{
    margin-right: 50px;
    @include max(1399px){
        margin-right: 30px;
    }
    @include max(991px){
        margin-right: 0;
    }
}
.ml-63{
    margin-left: 63px;
    @include max (991px){
        margin-left: 0;
    }
}
.mb-40{
    margin-bottom: 40px;
    @include max(991px){
        margin-bottom: 30px;
    }
}
.box-shadow {
    box-shadow: 0px 4px 80px 0px rgba(0, 0, 0, 0.08);
}
.sofax-aboutus-section-title{
    @include max(991px){
        text-align: center;
    }
}
.sofax-aboutus-content-text {
    margin-left: 115px;
    @include max (991px) {
      margin-left: 0;
      padding-top: 30px;
      text-align: center;
    }
}


.terms-condition{
    p{
        font-size: 20px!important;
    }
    .condition{
        color: var(--dark-bg);
        font-weight: 600;
        font-size: 20px;
        line-height: 28px;
    }
    
}
.mb130{
    padding-bottom: 130px;
    border-bottom: 1px solid var(--light-color);
    @include max (1199px){
        padding-bottom: 100px;
    }
    @include max (991px){
        padding-bottom: 80px;
    }
    @include max (767px){
        padding-bottom: 60px;
    }
}

.sofax-error-content{
    text-align: center;
    max-width: 780px;
    margin: 0 auto;
    img{
        margin-bottom: 80px;
        @include max (991px){
            margin-bottom: 60px;
        }
        @include max (767px){
            margin-bottom: 40px;
        }
    }
}