.location-icon {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.contactus-authore{
    &-wrapper{
        margin-top: 50px;
        @include max (991px){
            margin-top: 0;
            margin-bottom: 40px;
        }
    }
}
.responsive-map{
    overflow: hidden;
    position:relative;
    height: 600px;
    @include max (991px){
        height: 500px;
    }
    @include max (767px){
        height: 400px;
    }
}
.responsive-map iframe{
    left:0;
    top:0;
    height:100%;
    width:100%;
    position:absolute;
}

.sofax-field-box{
    padding: 40px 30px;
    border-radius: 30px;
    box-shadow: 0px 4px 80px 0px #00000014;
    @include max(991px) {
        padding: 30px;
        margin-top: 24px;
    }
    h3{
        margin-bottom: 30px;
    }
}

.sofax-main-field{
    margin-bottom: 24px;
    label{
        font-size: 24px;
        font-weight: 700;
        line-height: 32px;
        letter-spacing: -0.5px;
        display: inline-block;
        margin-bottom: 15px;

    }
}
.sofax-form-box{
    input{
        padding: 0;
        height: initial;
        width: initial;
        margin-bottom: 0;
        display: none;
        cursor: pointer;
    }
    label{
    position: relative;
    cursor: pointer;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--dark-bg);
    margin-bottom: 30px;
    @include max(767px){
        display: block;
        margin-bottom: 15px;

    }
        &::before{
            content: "";
            -webkit-appearance: none;
            background-color: transparent;
            border: 2px solid var(--body-color);
            padding: 7px;
            display: inline-block;
            position: relative;
            vertical-align: middle;
            cursor: pointer;
            margin-right: 12px;
            margin-top: -2px;
        } 
    }
}
.sofax-form-box input:checked+label:after {
    content: "";
    display: block;
    position: absolute;
    top: 7px;
    left: 6px;
    width: 5px;
    height: 9px;
    border: solid var(--dark-bg);
    border-width: 0 2px 2px 0;
    transform: rotate(45deg);
}
button#sofax-submit-btn{
    border-radius: 50px;
    &:hover{
        color: var(--white-color)!important;
        background-color: var(--dark-bg);
    }
}
.reset-pswrd{
    margin: 24px 0 0 0!important;
}