// bootstrap button style

.sofax-default-btn,
#sofax-submit-btn {
  font-size: $button_font_size;
  line-height: $buton_line_height;
  padding: $button_padding;
  font-weight: $button_font_weight;
  font-family: $button_font_family;
  width: fit-content;
  position: relative;
  z-index: 1;
  white-space: nowrap;
  text-overflow: ellipsis;
  display: inline-flex;
  text-align: center;
  -o-transition: all .4s ease-in-out;
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
  overflow: hidden;
  color: var(--heading-color) !important;
  background-color: var(--accent-color);
  position: relative;
  z-index: 0;
  overflow: hidden;
  position: relative;
  transform-style: preserve-3d;
  &.btn-hover{
    &:hover{
      background: var(--dark-bg);
      color: var(--white-color)!important;
    }
  }
  &.sofax-header-btn{
    padding: 13px 36px;
  }

  &.outline-btn{
    padding: 17px 40px;
    background-color: transparent;
    border: 1px solid var(--heading-color);
    transition: all 0.4s;
    &:hover{
      background: var(--accent-color);
      border: 1px solid var(--accent-color);
    }
  }
  &.dark-btn{
    color: var(--white-color)!important;
    background-color: var(--dark-bg);
  }
  &.disabled,
  &:disabled {
    opacity: 1;
  }

  &.d-block {
    width: 100%;
  }

  &.pill {
    border-radius: 50px;
  }

  &.btn-icon {
    padding: 9px 24px;
  }

  &:focus {
    box-shadow: none;
  }
  &.pricing-btn-v4:hover {
    color: var(--heading-color)!important;
    background-color: var(--accent-color);
  }
  &.pricing-btnv4:hover {
    color: var(--white-color)!important;
    background-color: var(--heading-color);
  }
  
}
.blog-btn{
  background-color: var(--light-color);
  color: var(--body-color)!important;
  font-weight: 400!important;
  padding: 7px 18px;
  border-radius: 50px;
}




//  sofax icon btn
a.sofax-icon-btn {
  font-size: 16px;
  line-height: 24px;
  font-weight: 600;
  color: var(--body-color);
  
  img{
      margin-left: 10px;
  }
}
// sofax subcribetion btn
.sofax-service-btn {
  display: flex;
  justify-content: center;
  align-items: center;
}
// sofax blog icon btn
.sofax-blog-icon-btn {
  font-size: 18px!important;
  line-height: 24px!important;
  font-weight: 700!important;
  color: var(--dark-bg)!important;
}

.sofax-hero-btn-wrap {
  display: flex;
  align-items: center;
  gap: 24px;
  justify-content: center;
  flex-wrap: wrap;
  position: relative;
}

.sofax-hero5-btn {
  justify-content: start!important;
  @include max (991px){
    justify-content: center!important;
  }
}
//  sofax portfolio btn
.sofax-portfolio-btn {
  text-align: center;
  margin-top: 80px;
  @include max(991px){
    margin-top: 60px;
  }
  @include max(767px){
    margin-top: 40px;
  }
}




.sofax-default-btn::before {
  position: absolute;
  content: '';
  border-radius: inherit;
  -webkit-transition: all 0.4s ease;
  transition: all 0.4s ease;
  z-index: -1;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-size: 102% 102%;
  opacity: 0;
}
.sofax-default-btn .button-wraper {
  transition: opacity 0.2s, transform 0.2s;
  transition-timing-function: cubic-bezier(0.455, 0.03, 0.515, 0.955);
}
.sofax-default-btn:hover .button-wraper {
  transform: translateY(-150%);
  opacity: 0;
}
.sofax-default-btn:hover::before {
  opacity: 1;
  transition: all 0.2s cubic-bezier(0.455, 0.03, 0.515, 0.955);
}
.sofax-default-btn:hover::after {
  transform: translate(0%, -50%);
  opacity: 1;
}


.sofax-default-btn:after {
  content: attr(data-text);
  display: inline-block;
  position: absolute;
  top: 50%;
  opacity: 0;
  line-height: 12.5px;
  transform: translate(0, 100%);
  transition: opacity 0.2s, transform 0.2s;
  transition-timing-function: cubic-bezier(0.455, 0.03, 0.515, 0.955);
  white-space: nowrap;
}

.portfolio-project-btn{
  a{
    margin-left: 24px;
   
    &:first-child{
      margin-left: 0;
    }
    @include max(991px){
      margin-bottom: 24px;
    }
  }
  .career-filter-btn{
    padding: 13px 30px;
  }
}
.sofax-404-bottom-btn{
  text-align: center;
}