.sofax-accordion{
    &-item{
      padding: 23px 24px;
      position: relative;
      margin-bottom: 24px;
      border-radius: 10px;
      transition: all 0.4s;
      border: 1px solid var(--light-color);
      &.about-us-section {
        border: 1px solid var(--dark-bg);
      }
      
      &.open .sofax-active-icon{
        transform: rotate(180deg);
        
      }
      &.open{
        border: 1px solid #fff;
        box-shadow: 0px 4px 80px 0px #00000014;
      }
      &.open{
        .sofax-accordion-body{
          display: block;
         p{
            font-size: 20px;
         }
        }
      }
    }
    &-header{
      cursor: pointer;
      position: relative;
      display: flex;
      justify-content: space-between;
      h3{
        @include max(767px) {
          font-size: 24px;
          padding-right: 25px;
        }
      }
     
    }
    &-body{
      display: none;
      padding-top: 15px;
      &.default{
        display: block;
      }
      @include max(991px) {
        padding: 24px 0 0;
      }
    }
    // End one
    &-wrap2{
        .sofax-accordion-item.open {
            border: 1px solid var(--light-color2);
            box-shadow: none;
        }
    }
    &-wrap3{
      .sofax-accordion-item{
        &.open {
          border: 1px solid var(--dark-bg);
          background: var(--dark-bg);
          box-shadow: none;
          h4{
            color: var(--white-bg);
          }
          p{
            color: var(--white-bg);
          }
        }
      }
  }
  }
  
  .sofax-accordion-item
  // .about-us-section{
  //   border: 1px solid var(--dark-bg!);
  // }
  .sofax-accordion-wrapper {
    display: flex;
    gap: 24px;
    @include max (991px){
        display: block;
    }
}

.sofax-active-icon{
    transition: all 0.4s;
    
    &.sofax-accordion-btn{
      img {
      display: inline-block;
      background: var(--accent-color);
      padding: 8px 4px;
      border-radius: 50%;
    }
  }
}
.sofax-accordion-section-wrapper{
    display: flex;
    gap: 24px;
    @include max (991px){
        display: block;
    }
}
.sofax-accordion-section-v2 {
    flex: 0 0 50%;
}
