.pricing-text {
    ul {
        li{
            position: relative;
            padding-left: 35px;
            font-weight: 600;
            font-size: 20px;
            line-height: 28px;
            color: #0E0E0E; 
            margin-bottom: 15px;
            img {
                position: absolute;
                left: 0;
            }
        }
    }
}
// end pricing section v1

.sofax-iconbox-icon {
    flex-shrink: 0;
} 
.sofax-content-iconbox-wrap {
    display: flex;
    gap: 20px;
    max-width: 600px;
    margin-bottom: 24px;
    @include max(991px){
        &:last-child{
            margin-bottom: 0;
        }
    }
    &.content-v5 {
        max-width: none;
        margin-bottom: 15px!important;
    }
}

// sofax content2 section
.sofax-list-icon {
    &-wrap {
        display: flex;
        gap: 20px;
        margin: 24px 0 0 15px;
        @include max (991px){
            margin: 24px 0 0 0; 
        }
        @include max (767px){
            display: block;
        }
    }
    &-icon {
        ul{
            li{
                position: relative;
                font-size: 23px;
                font-weight: 700;
                line-height: 32px;
                text-align: left;
                color: #0E0E0E;
                padding-left: 30px;
                margin-bottom: 24px;
                &:last-child{
                    margin-bottom: 0;
                }
                img {
                    position: absolute;
                    left: 0;
                    top: 5px;
                }
            }
        }
    }
}





/* top scroll */
.progress-wrap {
	position: fixed;
	right: 30px;
	bottom: 30px;
	height: 50px;
	width: 50px;
	cursor: pointer;
	display: block;
	border-radius: 50px;
	box-shadow: inset 0 0 0 2px rgba(0, 0, 0, 0.04);
	z-index: 10000;
	opacity: 0;
	visibility: hidden;
	transform: translateY(15px);
	-webkit-transition: all 200ms linear;
    transition: all 200ms linear;
}
.progress-wrap.active-progress {
	opacity: 1;
	visibility: visible;
	transform: translateY(0);
}

.progress-wrap svg path { 
	fill: none; 
}
.progress-wrap svg.progress-circle path {
	stroke: #0D0E1D; /* --- Lijn progres kleur --- */
	stroke-width: 4;
	box-sizing:border-box;
	-webkit-transition: all 200ms linear;
    transition: all 200ms linear;
}

.top-arrow {
    top: 50%;
    left: 50%;
    position: absolute;
    transform: translate(-50%, -50%);
}
