.sofax-portfolio-author {
    &-wrap {
        display: flex;
        justify-content: space-between;
        margin-bottom: 30px;
    }
    &-data{
        h4 {
            margin-bottom: 5px;
        }
    }
    &-icon{
        align-self: center;
        @include max (991px){
            img {
                width: 80%;
            }
        }
        @include max (767px){
            img {
                width: 70%;
            }
        }
    }
}
.sofax-portfolio-menu{
    ul{
        li{
            display: inline-block;
            border: 1px solid var(--dark-bg);
            border-radius: 50px;
            padding: 15px 40px;
            cursor: pointer;
            margin-left: 24px;
            font-size: 16px;
            margin-bottom: 24px;
            font-weight: 700;
            color: var(--dark-bg);
            &:last-child{
                margin-bottom: 0;
            }
            &.active{
                background: var(--accent-color);
                border: 1px solid var(--accent-color);
            }
        }
    }
}
.sofax-portfolio-wrap{
    margin: 0 24px 24px 24px;
}