.sofax-subscription-field{
  &-inner{
    position: relative;
    margin-bottom: 30px;
    input{
      margin: 0;
      height: 60px;
      width: 100%;
      color: var(--body-color);
      font-weight: 400;
      font-size: 16px;
      border-radius: 50px;
      background-color: var(--white-color)!important;
      padding: 15px 28px !important;
      transition: all .4s;
      letter-spacing: .5px;
      border: 1px solid var(--dark-bg) !important;
      &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
         color: var(--body-color);
       }
       &::-moz-placeholder { /* Firefox 19+ */
         color: var(--body-color);
       }
       &:-ms-input-placeholder { /* IE 10+ */
         color: var(--body-color);
       }
       &:-moz-placeholder { /* Firefox 18- */
         color: var(--body-color);
       }
    }
    .search-icon-img{
      position: absolute;
      top: 15px;
      right: 30px;
    }
  }
  &-categories{
    border: 1px solid var(--dark-bg);
    padding: 30px;
    border-radius: 20px;
    margin-bottom: 30px;
    h4{
      margin-bottom: 24px;
    }
    ul{list-style: show!important;
      li{
        margin-bottom: 15px!important;
        &:last-child{
            margin-bottom: 0!important;
          }
        a{
          color: var(--body-color);
          transition: all 0.3s;
          &:hover{
            color: var(--accent-color);
          }
        }
      }
    }
  }
  &-post{
    border: 1px solid var(--dark-bg);
    padding: 30px;
    border-radius: 20px;
    margin-bottom: 30px;
    h4{
      margin-bottom: 24px;
    }
    .title-post-thumb{
        display: flex;
        gap: 15px;
        margin-bottom: 15px;
      }
    .title-post-content{
      ul{
        li{
          font-size: 12px;
          line-height: 16px;
          margin-bottom: 10px;
          color: var(--body-color);
        }
      }
    }
  }
  &-group{
    border: 1px solid var(--dark-bg);
    padding: 30px;
    border-radius: 20px;
    margin-bottom: 30px;
    h4{
      margin-bottom: 24px;
    }
    a{
      margin: 0 5px 10px 0;
      font-size: 18px!important;
      line-height: 24px;
      padding: 13px 22px!important;
    }
  }
  &-blog-inner-footer{
    background: var(--dark-bg);
    padding: 30px;
    border-radius: 20px;
    h4{
      margin-bottom: 24px;
    }
  }
  &-inner-button{
    input{
      border: 1px solid var(--light-color)!important;
      color: var(--white-color)!important;
      font-size: 18px!important;
      line-height: 28px;
      background: transparent!important;
      font-weight: 400;
      &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
        color: var(--white-color);
      }
      &::-moz-placeholder { /* Firefox 19+ */
        color: var(--white-color);
      }
      &:-ms-input-placeholder { /* IE 10+ */
        color: var(--white-color);
      }
      &:-moz-placeholder { /* Firefox 18- */
        color: var(--white-color);
      }
    }
  }
}
