.sofax-hero {
    &-shape {
        position: absolute;
        left: -2%;
        bottom: 102%;
        overflow: hidden;
        transform: translatey(0px);
        -webkit-animation: float 2s ease-in-out infinite;
        animation: float 3s ease-in-out infinite;
        @include max (1399px) {
            left: -7%;
            bottom: 102%;
        }
        @include max (1199px) {
            left: -7%;
            bottom: 105%;
            width: 20%;
        }
        @include max (991px) {
            display: none;
        }
        img {
            width: 100%;
            height: auto;
        }
    }
   
    // end sofax hero shape one v1
    &-shape2 {
        position: absolute;
        right: -15%;
        bottom: 68%;
        overflow: hidden;
        transform: translatey(0px);
        -webkit-animation: float 2s ease-in-out infinite;
        animation: float 3s ease-in-out infinite;
        img {
            width: 100%;
            height: auto;
        }
        @include max (1399px) {
            right: -15%;
            bottom: 63%;
        }
        @include max (1199px) {
            right: -11%;
            top: 6%;
            width: 20%;
        }
        @include max (991px) {
            display: none;
        }
    }
}
