.sofax-hero-thumb {
    position: relative;
    padding-top: 80px;
    @include max(991px) {
        padding-top: 60px;
        text-align: center;
        img {
            width: 100%;
        }
    }
    @include max(767px) {
        padding-top: 40px;
    }
}
//  end sofax hero thumb v1

.sofax-content-img {
    position: relative;
    text-align: center;
    @include max(991px) {
        margin-bottom: 40px;
        img {
            width: 100%;
        }
    }
}

// end sofax content thumb v1

.sofax-features-thumb {
    text-align: center;
