//  hero5 thumb
.hero5-thumb {
    position: relative;
    padding: 0 50px;
    img {
        box-shadow: 8px 8px 0px 0px var(--dark-bg);
        border-radius: 8px;
    }
}
.about-thumbv5 {
    @include max (991px){
        margin-bottom: 40px;
    }
    img {
        box-shadow: 8px 8px 0 0 var(--dark-bg);
        border-radius: 8px;
        @include max (991px){
            width: 100%;
        }
    }
}
// sofax portfolio thumb v5
.sofax-portfolio-thumb {
    margin-bottom: 24px;
   @include max (991px){
    img {
        width: 1000%;
    }
   }
}
// sofax about us thumb
.sofax-aboutus-thumb{
    position: relative;
    @include max(991px){
        padding-top: 60px;
    }
    @include max(991px){
        padding-top: 40px;
    }
    img {
        box-shadow: 8px 8px 0 0 var(--dark-bg);
        border-radius: 8px;
    }
}

.sofax-inner-service {
    &-thumb{
        margin-bottom: 40px;
        @include max(991px){
            margin-bottom: 30px;
        }
        img{
            box-shadow: 8px 8px 0 0 var(--dark-bg);
            border-radius: 10px;
            @include max(991px){
                width: 100%;
            }
        }
    } 
}
.sofax-service-content{
    &-thumb{
        box-shadow: 8px 8px 0 0 var(--dark-bg);
        border-radius: 8px;
        margin-bottom: 80px;
        @include max (991px){
            margin-bottom: 60px;
        }
        @include max (767px){
            margin-bottom: 40px;
        }
    }
}
.sofax-inner-content{
    &-thumb{
        @include max (991px){
            margin-top: 40px;
        }
        img{
            box-shadow: 8px 8px 0 0 var(--dark-bg);
            border-radius: 8px;
            @include max (991px){
                width: 100%;
            }   
        }
    }
}
.sofax-career{
    &-thumb{
        padding-bottom: 100px;
        border-bottom: 1px solid var(--light-color);
        margin-bottom: 100px;
        @include max (991px){
            padding-bottom: 70px;
            margin-bottom: 70px;
        }
        @include max (767px){
            padding-bottom: 40px;
            margin-bottom: 40px;
        }
    }
}