
  /* end  sofax pricing section */

  .sofax-pricing{
    &-wrap{
        border-radius: 10px;
        padding: 36px;
        margin-bottom: 24px;
        transition: all 0.4s;
        background-color:  var(--white-bg);
        box-shadow: 0px 4px 80px 0px #00000014;
        @include max(991px) {
            padding: 30px;
        }
        // &:hover{
            
        // }
        // &.active{
        //     box-shadow: 0px 4px 80px 0px #00000014;
        // }
    }
    &-header{
        margin-bottom: 15px;
        img{
            margin-bottom: 30px;
        }
    }
    &-price{
        gap: 10px;
        display: flex;
        align-items: flex-end;
        padding-bottom: 24px;
        margin-bottom: 24px;
        border-bottom: 1px solid var( --light-color);
    }
    &-body{
        margin-bottom: 40px;
        h5{
            font-weight: 600;
            margin-bottom: 20px;
        }
        ul{
            li{
                position: relative;
                font-weight: 600;
                padding-left: 40px;
                margin-bottom: 15px;
                &:last-child{
                    margin-bottom: 0;
                }
                img{
                    position: absolute;
                    left: 0;
                    top: 5px;
                }
            }
        }
    }
    &-bodyv4 {
        margin-bottom: 0!important;
    }
  }
//  sofax pricing section v4




.sofax-pricing-button-wrap {
  text-align: center;
  margin-bottom: 40px;
  @include max(991px){
    margin-bottom: 30px;
  }
}
.toggle, .toggler {
  display: inline-block;
  vertical-align: middle;
  margin: 5px;
}
.toggler {
  font-weight: 600;
  color: var(--dark-bg);
}
.toggler--is-activee {
  color: var(--dark-bg);
}
.b {
  display: block;
}
.toggle {
  position: relative;
  width: 70px;
  height: 36px;
  border-radius: 50px;
  background-color: var(--accent-color);
  overflow: hidden;
  box-shadow: inset 0 0 2px 1px rgba(0, 0, 0, 0.05);
}
.check {
  position: absolute;
  display: block;
  cursor: pointer;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: 6;
}
.check:checked ~ .switch {
  right: 5px;
  left: 57.5%;
  transition: 0.25s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  transition-property: left, right;
  transition-delay: .08s, 0s;
}
.switch {
  position: absolute;
  left: 5px;
  top: 5px;
  bottom: 5px;
  right: 56.5%;
  background-color: var(--dark-bg);
  border-radius: 30px;
  z-index: 1;
  transition: 0.25s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  transition-property: left, right;
  transition-delay: 0s, .08s;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
}
.hide{
  display: none;
}



  