// testimonial slider v2

.sofax-testimonial {
  &-rating {
    margin-bottom: 30px;
    @include max (991px) {
      margin-bottom: 20px;
    }
    ul {
      li{
        display: inline-block;
      }
    }
  }
}

.testimonial {
  &-content {
    margin-bottom: 40px;
  }
  p {
    font-size: 30px;
    line-height: 40px;
  }
}

.testimonial-author {
  margin-right: 30%;
  &-icon {
    ul {
      li {
        display: inline-block;
        margin-right: 24px;
        a{
          svg {
            width: 50px;
            height: 50px;
            padding: 5px;
          }
        }
      }
    }
  }
}

.sofax-testimonial-2column-slider .sofax-t-next:hover,
.sofax-testimonial-2column-slider .sofax-t-prev svg {
  background-color: var(--green-bg);
}
.sofax-testimonial-2column-slider .sofax-t-prev:hover {
  background-color: var(--green-bg);
}
.sofax-testimonial-2column-slider .sofax-t-next, 
.sofax-testimonial-2column-slider .sofax-t-prev {
  left: 76%;
  width: 50px;
  height: 50px;
  bottom: 25px;
  z-index: 99;
  cursor: pointer;
  position: absolute;
  background-size: 30px;
  background-position: center;
  background-repeat: no-repeat;
  transition: all .4s;
  background-image: url(../images/v2/leftarrow.svg);
  border: none;
  background-color: var(--light-color);
  border-radius: 3px;
  @include max(1399px) {
    left: 75%;
    bottom: 15px;
  }
  @include max(1199px) {
    left: 72%;
    bottom: 13px;
  }
  @include max(991px) {
    display: none!important;
  }
  
}
.sofax-testimonial-2column-slider .sofax-t-prev {
  left: auto;
  right: 15%;
  background-image: url(../images/v2/rightarrow.svg);
  background-color: var(--light-color);
}
.sofax-testimonial-slider-content p{
  font-size: 35px;
  line-height: 45px;
  @include max (1199px)
  {
    font-size: 28px;
    line-height: 36px;
  }
  @include max (767px)
  {
    font-size: 25px;
    line-height: 32px;
  }
}
.testimonial-author p {
  font-size: 18px;
}



