// testimonial v3 
.sofax-testimonial{
  &-content-wrap {
    padding: 30px;
    border: 1px solid var(--light-color);
    border-radius: 10px;
    margin-bottom: 24px;
  }
  &-btn {
    text-align: center;
    margin-top: 80px;
    @include max (991px) {
      margin-top: 60px;
    }
    @include max (767px){
      margin-top: 40px;
    }
  }
}
// sofax testimonial section v4
.testimonial-content-v4 p {
  font-size: 35px!important;
  line-height: 45px;
  letter-spacing: -0.5px;
  color: var(--dark-bg)!important;
  margin-bottom: 40px;
  @include max (991px) {
    margin-bottom: 30px;
    font-size: 30px!important;
    line-height: 35px;
  }
  @include max (767px) {
    font-size: 24px!important;
    line-height: 30px;
  }
}
//  sofax tesimonial v4
.sofax-testimonial-author-data-v4{
  h4{
    margin-bottom: 8px;
  }
  @include max (991px){
    text-align: center;
  }
} 
// sofax testimonal slider content v5
.sofax-default {
  &-contentv5{
    p {
      max-width: 630px;
      @include max (991px){
        max-width:100%;
      }
    }
  }
}
.sofax-testimonial-slider{
  &-wrapv5{
    padding: 30px;
    border: 1px solid var(--dark-bg);
    border-radius: 5px;
    box-shadow: 5px 5px 0 0 var(--dark-bg);
    margin: 0 10px 10px 0;
    @include max (991px){
      margin-top: 40px;
    }
  }
  &-ratingv5{
    margin-bottom: 15px;
    ul{
      li{
        display: inline-block;
      }
    }
  }
  &-datav5{
    margin-bottom: 24px;
  }
  &-authore-wrap {
    display: flex;
    gap: 10px;
  }
}

.sofax-testimonial-2column-sliderv5 .sofax-t-next:hover,
.sofax-testimonial-2column-sliderv5 .sofax-t-prev svg {
  background-color: var(--accent-color);
  border: 1px solid var(--accent-color);
}
.sofax-testimonial-2column-sliderv5 .sofax-t-prev:hover {
  background-color: var(--accent-color);
  border: 1px solid var(--accent-color);
}
.sofax-testimonial-2column-sliderv5 .sofax-t-next, 
.sofax-testimonial-2column-sliderv5 .sofax-t-prev {
  left: 0;
  bottom: -15%;
  width: 50px;
  height: 50px;
  border-radius: 50px;
  z-index: 99;
  cursor: pointer;
  position: absolute;
  background-size: 30px;
  background-position: center;
  background-repeat: no-repeat;
  transition: all .4s;
  background-image: url(../images/v5/left-arrow.svg);
  border: 1px solid var(--dark-bg);
  @include max(1399px) {
    left: 0;
    bottom: -5%;
  }
  @include max(1199px) {
    left: 0;
    bottom: 15%;
  }
  @include max(991px) {
    display: none!important;
  }
}
.sofax-testimonial-2column-sliderv5 .sofax-t-prev {
  left: 6%;
  bottom: -15%;
  background-image: url(../images/v5/right-arrow.svg);
  @include max (1399px){
    left: 7%;
    bottom: -5%;
  }
  @include max (1199px){
    left: 7%;
    bottom: 15%;
  }
}
