.sofax-section-padding2 {
    width: 100%;
}
.sofax-counter {
    &-wrap{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        gap: 30px;
        text-align: center;
    }
    &-data {
        h2 {
            color: var(--white-bg);
        }
        p {
            color: var(--white-bg);
        }
    }
}
// sofax counter v5
.sofax-counter{
    &-wrap-v5 {
        display: flex;
        gap: 38px;
        justify-content: space-between;
        @include max (767px){
            display: block;
        }
        h2 {
            margin-bottom: 5px!important;
        }
    }
    &-data-v5 {
        h2 {
            @include max(991px){
                font-size: 60px!important;
                line-height: 65px;
                margin: 0;
            }
            @include max(767px){
                margin: 0!important;
            }
        }
        @include max (767px) {
            margin-bottom: 20px;
            font-size: 45px!important;
            line-height: 50px;
        }
    }
}
.sofax-countdown{
    &-wrap {
        display: flex;
        gap: 24px;
        justify-content: center;
        flex-wrap: wrap;
    }
    &-item{
        background-color: #FFFFFF;
        border-radius: 010px 010px 010px 010px;
        box-shadow: 0px 4px 80px 0px rgba(0, 0, 0, 0.08);
        padding: 035px 55px 035px 055px;
        text-align: center;
        font-size: 75px;
        line-height: 80px;
        font-weight: 700;
        color: var(--dark-bg);
        p{
            text-align: center;
            color: var(--accent-color);
            font-weight: 600;
            font-size: 21px;
            line-height: 30px;
        }
    }
}