.sofax-career-content{
    &-wrapper{
        border: 1px solid var(--light-color);
        padding: 30px;
        border-radius: 10px;
        margin: 0 24px 24px 24px;
        transition: 0.6s;
        &:hover{
            p,h4,h6{
                color: var(--white-color);
            }   
            background: var(--dark-bg);
            border: 1px solid var(--light-color2)!important;
            .sofax-career-content-icon svg{
                background-color: var(--accent-color);
                path{
                    fill: var(--dark-bg);
                }
            }
            .sofax-career-content-icon2 svg path{
                fill: var(--white-color);
            }
        }
    }
    &-data{
        h4{
            margin-bottom: 10px;
        }
    }
    &-text{
        margin-bottom: 24px;
        p{
            font-size: 20px!important;
        }
    }
    &-text2{
        h6{
            font-size: 18px!important;
            line-height: 28px;
            font-weight: 600;
        }
    }
    &-autohre-wrap{
        display: flex;
        justify-content: space-between;
        padding-bottom: 24px;
        border-bottom: 1px solid var(--light-color2);
        margin-bottom: 24px;
    }
    
    &-icon{
        display: flex;
        align-items: center;
        svg{
            display: flex;
            align-items: center;
            justify-content: center;
            background: var(--dark-bg);
            width: 40px;
            height: 40px;
            border-radius: 50px;
            padding: 10px;
            transition: all 0.4s;
            cursor: pointer;
        }
    }
    &-icon-text-wrap{
        display: flex;
        align-items: center;
        gap: 8px;
    }
    &-icon-text-wrapper2{
        display: flex;
        gap: 24px;
    }
}


// career datails
.sofax-career-details1{
    margin-bottom: 40px;
}
.career-details{
    margin-bottom: 24px;
    h2{
        margin-bottom: 15px!important;
    }
}
.sofax-career-details{
    &-content{
        margin-bottom: 40px;
        @include max (991px){
            margin-bottom: 30px;
        }
        &:last-child{
            margin-bottom: 0;
        }
        h3{
            margin-bottom: 15px;
        }
        p{
            margin-bottom: 24px;
            &:last-child{
                margin-bottom: 0;
            }
        }
    }
    &-data{
        ul{
            li{
                margin-bottom: 15px;
                &:last-child{
                    margin-bottom: 0;
                }
            }
        }
    }
}
