.sofax-team-member{
    &-wrap{
        margin-bottom: 24px;
    }
    &-img {
        margin-bottom: 24px;
        img {
            width: 100%;
        }
    }
    &-content{
        text-align: center;
    }
    &-aboutus{
        margin-top: 120px;
        @include max (991px){
            margin-top: 0;
        }
    }
    
}
.team-member{
    &-content{
        margin-bottom: 40px;
    }
    h2{
        margin-bottom: 15px!important;
    }
}
.team-member-details{
    &-wrap{
        display: flex;
        gap: 40px;

        @include max(767px){
            width: 100%;
            gap: 20px;
        }
    }
    &-data{
        h4{
            margin-bottom: 24px;
            &:last-child{
                margin-bottom: 0;
            }
        }
    }
}
.member-social-contuct{
    &-wrap {
        display: flex;
        gap: 25px;
        margin-top: 40px;
        @include max(767px){
            margin-top: 25px;
        }
    }
    &-data{
        display: flex;
        align-items: center;
    }
}
.sofax-member{
    &-experience{
        margin-top: 80px;
        @include max (991px){
            margin-top: 60px;
        }
        @include max (991px){
            margin-top: 40px;
        }
        h3{
            margin-bottom: 30px;
        }
    }
}
.our-teaminner {
    margin-left: 0!important;
}

.sofax-member{
    &-skill-wrap{
        margin-left: 60px;
        @include max (991px){
            margin-left: 0;
            margin-top: 40px;
        }
        @include max (767px){
            margin-top: 30px;
        }
    }
    &-bar{
        margin-bottom: 10px;
    }
    &-progress-line{
        position: relative;
        height: 10px;
        width: 550px;
        background: var(--light-color);
        border-radius: 10px;
        transform: scaleX(0);
        transform-origin: left;
        animation: animate 1s cubic-bezier(1, 0, 0.5, 1) forwards;
        margin-bottom: 20px;
        &:last-child{
            margin-bottom: 0;
        }
        @include max (767px){
            width: 100%;
        }
        h5{
            position: absolute;
            background: var(--accent-color);
            height: 100%;
            border-radius: 10px;
            transform: scaleX(0);
            transform-origin: left;
            animation: animate 1s 1s cubic-bezier(1, 0, 0.5, 1) forwards;
            
        }
        h5::before{
            position: absolute;
            content: "";
            height: 0;
            right: 0;
            top: -14px;
            width: 0;
            border: 7px solid transparent;
            border-bottom-width: 0px;
            border-right-width: 0px;
            border-top-style: #f0f0f0;
        }
        h5::after{
            position: absolute;
            right: -32px;
            top: -30px;
            color: var(--dark-bg);
            font-size: 20px;
            font-weight: 700;
            padding: 1px 8px;
            border-radius: 3px;
        }
        .html{
            width: 87%;
        }
        .html::after{
            content: "87%";
        }
        .css{
            width: 90%;
        }
        .css::after{
            content: "90%";
        }
        .js{
            width: 60%;
        }
        .js::after{
            content: "60%";
        }
    }
    h3{
        margin-bottom: 30px;
    }
}
@keyframes animate {
    100% {
    transform: scaleX(1);
    }
}
  
  
  