.wrapper {
  width: 1000px;
  margin: auto;
  display: block;
}

.btn {
  text-transform: uppercase;
  font-size: 12px;
  padding: 5px 15px;
  background: #d6d6d6;
  display: inline-block;
  margin-bottom: 10px;
  text-decoration: none;
  color: #fff;
}
.btn:hover {
  background: #aaa;
}

/* ---- button ---- */

.button {
  display: inline-block;
  padding: 10px 18px;
  margin-bottom: 10px;
  background: #EEE;
  border: none;
  border-radius: 7px;
  background: #f3f3f3;
  color: #222;
  font-family: sans-serif;
  font-size: 16px;
  text-shadow: 0;
  cursor: pointer;
}

.button:hover {
  background-color: #8CF;
  color: #222;
}

.button:active,
.button.is-checked {
  background-color: #aa96ca;
}

.button.is-checked {
  color: white;
}


/* ---- button-group ---- */

.button-group:after {
  content: '';
  display: block;
  clear: both;
}

.button-group .button {
  float: left;
  border-radius: 0;
  margin-left: 0;
  margin-right: 1px;
  font-family: arial;
  text-transform: uppercase;
  font-size: 12px;
  letter-spacing: 1px;
}


/* ---- isotope ---- */

.grid {
  border: 0;
}

/* clear fix */
.grid:after {
  content: '';
  display: block;
  clear: both;
}

/* ---- .element-item ---- */

.element-item {
  position: relative;
  float: left;
  width: 300px;
  height: 300px;
  margin: 5px;
  padding: 0;
  background: #888;
  color: #262524;
  overflow: hidden;
}
.element-item img {
  max-width: 100%;
  height: auto;
}

.element-item > * {
  margin: 0;
  padding: 0;
}

.element-item .name {
  position: absolute;

  left: 10px;
  top: 60px;
  text-transform: none;
  letter-spacing: 0;
  font-size: 12px;
  font-weight: normal;
}

.element-item .city {
  position: absolute;
  left: 10px;
  top: 0px;
  font-size: 42px;
  font-weight: bold;
  color: white;
}

.element-item .date {
  position: absolute;
  right: 8px;
  top: 5px;
}

.element-item.city1          { background: #F00; background: hsl(   0, 100%, 50%); }
.element-item.city2  { background: #F80; background: hsl(  36, 100%, 50%); }
.element-item.city3      { background: #FF0; background: hsl(  72, 100%, 50%); }